.main-content-project {
    padding-left: 240px;
    margin-left: 20px;
    background-color: #0a192f;
    height: 100vh;
    overflow-y: scroll;
    display: block;
    overflow: hidden;
}

.text-container-project {
    display: flex;
}

.text-heading-project {
    margin-top: 50px;
    display: flex;
    height: fit-content;
}
.project-img {
    width: 500px;
    height: 300px;
    border-radius: 3px;
}

.project-img-left {
    right: 80px;
    width: 500px;
    height: 300px;
    border-radius: 3px;
    position: relative;
}

.project-main-container {
    margin-top: 50px;
}

.project-card  {
    margin-top: 50px;
    display: flex;
}

.project-title {
    font-size: 28px;
}

.project-title-flare {
    margin: 10px 0px;
}

.project-card-text-container {
    right: 80px;
    position: relative;
    width: fit-content;
    text-align: right;
}
.project-card-text-container-left {
    position: relative;
    width: fit-content;
    text-align: left;
}

.project-card-desc {
    text-align: right;
    background-color: #112240;
    color: #d8f4cd;
    font-size: 18px;
    box-shadow: 0 10px 30px -15px #020c1bb3;
    z-index: 2;
    padding: 25px;
    border-radius: 4px;
}

.project-card-desc-left {
    text-align: left;
    background-color: #112240;
    color: #d8f4cd;
    font-size: 18px;
    box-shadow: 0 10px 30px -15px #020c1bb3;
    z-index: 2;
    padding: 25px;
    border-radius: 4px;
    position: relative;
}


.project-tech-list {
    justify-content: flex-end;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 2;
    margin: 25px 0px 10px;
    padding: 0px;
    list-style: none;
}

.project-tech-list li {
    text-align: right;
    color: #d8f4cd;
    font-family: 'whitney-book';
    font-size: 16px;
    white-space: nowrap;
    margin: 0px 0px 5px 20px;
}

.project-tech-list-left {
    justify-content: flex-start;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 2;
    margin: 25px 0px 10px;
    padding: 0px;
    list-style: none;
}

.project-tech-list-left li {
    text-align: left;
    color: #d8f4cd;
    font-family: 'whitney-book';
    font-size: 16px;
    white-space: nowrap;
    margin: 0px 20px 5px 0px;
}

.project-links {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    position: relative;
    margin-top: 10px;
    -webkit-box-pack: end;
    justify-content: flex-end;
    margin-left: 0px;
    margin-right: -10px;
}

.project-links-left {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    position: relative;
    margin-top: 10px;
    -webkit-box-pack: end;
    justify-content: flex-start;
    margin-left: 0px;
    margin-right: -10px;
}

.project-icn {
    color: white;
    font-size: 2.2em;
    padding: 10px;
}


